.item{
    height: 100vh;
    width: 100vw;
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
    display: flex;
    justify-content: center;
    scroll-snap-align: start;
}

.item__container{
    width: 40vw;
    display: flex;
    flex-direction: column;
    align-items: center;
}
html::-webkit-scrollbar {
    width: 0px; /* For Chrome, Safari, and Opera */
}

.item__text{
    margin-top: 20%;
    display: flex;
    clear: both;
    overflow: hidden;
    white-space: nowrap;
    flex-direction: column;
    font-size: 34px;
    color: #393c41;
    align-items: center;
    flex: 1;
}

.item__textDesc{
    font-weight: 200;
    font-size: 14px;
}

.item__lowerThird{
    margin-bottom: 5%;
    display: flex;
    flex-direction: column;
}
.item__buttons{
    display: flex;
}
.item__expand{
    color: white;
    width: 100%;
    margin-top: 3%;
    display: flex;
    justify-content: center;
}